"use client"

import { useState, useEffect } from "react"
import Link from "next/link"
import { usePathname } from "next/navigation"
import { LiquidGlass } from "@/components/liquid-glass"

export function Navigation() {
  const [scrolled, setScrolled] = useState(false)
  const pathname = usePathname()

  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 50)
    }
    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  const isActive = (path: string) => pathname === path

  return (
    <nav className="fixed top-0 left-0 right-0 z-50 transition-all duration-300">
      <div className="max-w-6xl mx-auto px-6 lg:px-8 py-4">
        <div
          className={`relative overflow-hidden rounded-full border ${
            scrolled
              ? "border-white/70 shadow-[0_18px_90px_-60px_rgba(0,0,0,0.6)]"
              : "border-white/50 shadow-[0_10px_40px_-40px_rgba(0,0,0,0.5)]"
          }`}
          style={{ height: 72 }}
        >
          <div className="absolute inset-0">
            <LiquidGlass height={72} src="/modern-tokyo-cityscape-night.jpg" />
          </div>
          <div className="pointer-events-none absolute inset-0 bg-white/6 mix-blend-screen" />

          <div className="relative flex items-center justify-between px-6 py-3">
            <Link href="/" className="text-sm font-medium hover:opacity-70 transition-opacity">
              Portfolio
            </Link>

            <div className="flex items-center gap-8">
              <Link
                href="/work"
                className={`text-sm transition-colors ${
                  isActive("/work") ? "text-foreground" : "text-muted-foreground hover:text-foreground"
                }`}
              >
                Work
              </Link>
              <Link
                href="/photos"
                className={`text-sm transition-colors ${
                  isActive("/photos") ? "text-foreground" : "text-muted-foreground hover:text-foreground"
                }`}
              >
                Photos
              </Link>
              <Link
                href="/travel"
                className={`text-sm transition-colors ${
                  isActive("/travel") ? "text-foreground" : "text-muted-foreground hover:text-foreground"
                }`}
              >
                Travel
              </Link>
              <Link
                href="/contact"
                className={`text-sm transition-colors ${
                  isActive("/contact") ? "text-foreground" : "text-muted-foreground hover:text-foreground"
                }`}
              >
                Contact
              </Link>
            </div>
          </div>
        </div>
      </div>
    </nav>
  )
}
